main.products_main {
  background: url(../../assets/products/geometry.png);
  background-size: cover;
}

main.products_main > div.page_wrapper {
  background-color: rgba(255, 255, 255, 0.9);
}

main.products_main > div.page_wrapper > section.unbiased {
  padding: calc(var(--base-padding) * 2);
  max-width: 1200px;
  margin: 0 auto;
  margin-bottom: 5em;
  background-color: rgba(255, 255, 255, 0.95);
}

main.products_main > div.page_wrapper > section.unbiased > h1 {
  font-size: 45px;
  text-align: center;
}

main.products_main > div.page_wrapper > section.unbiased  p {
  line-height: 30px;
  word-spacing: 9px;
  letter-spacing: 0.2px;
  font-weight: 300;
  font-size: 1.3em;
  text-align: justify;
}



/*========================*/
/* AI Advertising Section */
/*========================*/
main.products_main > div.page_wrapper > section.advertising {
  padding: calc(var(--base-padding) * 2);
  max-width: 1200px;
  margin: 0 auto;
  margin-bottom: 5em;
  background-color: rgba(255, 255, 255, 0.95);
}

main.products_main > div.page_wrapper > section.advertising > h1 {
  font-size: 45px;
  text-align: center;
}

main.products_main > div.page_wrapper > section.advertising p {
  line-height: 30px;
  font-weight: 300;
  font-size: 1.3em;
  word-spacing: 9px;
  letter-spacing: 0.2px;
  text-align: justify;
}


@media (max-width: 900px) {
  main.products_main {
    background: url(../../assets/products/geometry.png);
    background-size: contain;
  }

  main.products_main > div.page_wrapper > section.unbiased {
    background-color: rgba(255, 255, 255, 0.5);
  }

  main.products_main > div.page_wrapper > section.advertising {
    background-color: rgba(255, 255, 255, 0.5);
  }
}