main.contact_us_main {
  background: url(../../assets/products/geometry.png);
  background-size: contain;
}

div.contact_us_main_container{
  margin: 0 auto;
  display: flex;
  gap: calc(var(--base-gap) * 1);
  flex-direction: column;
  padding: calc(var(--base-padding) * 2);
  margin-top: 2em;
  background-color: rgba(255, 255, 255, 0.95);
}

@keyframes slide-in {
  from {
    transform: translateX(-100%);
  }
  to {
    transform: translateX(0);
  }
}

div.contact_us_main_container > h1 {
  background-color: rgba(255, 255, 255, 0.75);
  padding: 1em;
  animation: slide-in 1s ease-in-out;
}

@keyframes slide-up {
  from {
    transform: translateY(100%);
  }
  to {
    transform: translateY(0);
  }
}

div.contact_us_main_container > div.address_section {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: calc(var(--base-gap) * 1.5);
  align-items: center;
  background-color: rgba(255, 255, 255, 0.8);
  max-width: 1200px;
  margin: 0 auto 2em;
  width: 100%;
  animation: slide-up 1s ease-in-out;
}

div.left {
  display: flex;
  gap: calc(var(--base-gap) * 2);
  flex-direction: column;
}

div.left > div.contact_info {
  display: flex;
  flex-direction: column;
  gap: var(--base-gap);
  width: max-content;
}


div.left > div.contact_info * {
  margin: 0;
}

div.left > div.contact_info > span{
  display: flex;
  flex-direction: row;
  gap: var(--base-gap);
}

div.right > img{
  width: 100%;
  border-radius: 5px;
}





/*=================*/
/* Message Section */
/*=================*/
div.contact_us_main_container > section.message_section {
  background-color: rgba(255, 255, 255, 0.8);
}

div.contact_us_main_container > section.message_section > form{
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  align-items: center;
  max-width: 600px;
  /* padding: calc(var(--base-padding) * 2); */
  padding: 0 !important;
  border-radius: 5px;
  gap: var(--base-gap);

  /* border: 1px solid grey; */
}

section.message_section > form > *{
  width: 100%;
}

section.message_section > form > span.top_section {
  display: flex;
}

form > span.top_section > *{
  width: 49%;
}

form > span.top_section > * > *{
  width: 100%;
}

section.message_section > form input, textarea{
  padding: 10px;
  border-radius: 5px;
  border: 1px solid orange;
  font-size: 1em;
}

section.message_section > form input:focus, textarea:focus {
  outline: 2px solid orange;
}

span.submit_btn_container {
  display: flex;
}

span.submit_btn_container > button{
  padding: calc(var(--base-padding) / 1.2) var(--base-padding);
  border-radius: 5px;
  font-size: 0.8em;
  width: 50%;
  margin: auto;
  transition: all 500ms;
  cursor: pointer;
  font-size: 1em;
}

span.submit_btn_container > button:hover{
  background: linear-gradient( 92.34deg, #F26522  -35.71%,  #FFDE17 100.76%);
}

/* section.message_section > form > * > *:not(.submit_btn_container){
  width: 100%;
} */






/*================================*/
/* Media Query For Mobile Devices */
/*================================*/
@media (max-width: 800px){
  div.contact_us_main_container{
    background-color: rgba(255, 255, 255, 0.9);
  }

  div.address_section {
    /* grid-template-columns: 1fr !important;
    direction:ltr; */
    display: flex !important;
    flex-direction: column-reverse !important;
    /* background-color: red; */
    
    text-align: center;
  }

  div.address_section > div.right {
    width: 100vw;
  }

  div.address_section > div.right > img {
    border-radius: 0;
  }

  div.contact_info {
    margin: 0 auto;
  }

  div.contact_us_main_container > section.message_section > form {
    padding: var(--base-padding);
  }
}