main > div.hero-video-container::-webkit-scrollbar{
  display: none;
}

main > div.hero-video-container{
  width: 100%;
  z-index: -1;
  position: fixed;
  /* position: absolute; */
  height: 100vh;
  overflow-x:hidden;
  /* background-image: url(../../assets/home/images/Live-Wave.svg); */
  background-position: center;
  background-size: cover;
  background-color: rgba(0,0,0,0.9) !important;
  /* background-color: red !important; */
  background-color: blue;
  height: 100vh;
  /* background-repeat: no-repeat; */
  /* background-size: 150%; */
}

/* main > div.hero-video-container > img{
  height: 100vh;
} */

main > div.hero-video-container > video{
  height: 100vmax;
  /* background-color: rgba(0,0,0,0.9) !important; */
  /* z-index: -2; */
  /* min-width: 100vmax; */
}

main > div.hero-video-container > video::-webkit-media-controls-play-button {
  display: none;
}

div.animate__animated {
  /* background-color: red;
  z-index: -1 !important; */
  display: block;
}

@keyframes hero-text-slide-in {
  0%{
    transform: translateX(-100%);
  }
  85%{
    transform: translateX(100px);
  }
}

main > div.hero-video-container > div.hero-text{
  position: absolute;
  color: white;
  bottom: 26%;
  padding: 0 calc(var(--base-padding) * 4);
  max-width: 1200px;
  animation: hero-text-slide-in 2s ease-in-out;

}

div.hero-text > h2{
  font-size: 80px;
  font-weight: 600;
  margin: 0;
  text-align: left;
  color: white;
  text-shadow: #3615BD;
  /* -webkit-text-stroke: 0.7px blue; */

}

main > a{
  border: none;
  position: fixed;
  /* float: left; */
  top: 83%;
  left: 2em;
}

div.hero-text > p{
  font-size: 2em;
  margin: 0;
  text-align: left;
  font-weight: 100;
}


div.spacer{
  height: 100vh;
  z-index: -2 !important;
}






/*===============*/
/* Intro Section */
/*===============*/
section.intro{
  text-align: left;
  top: 100vh;
  background-color: white;
  padding: 3em 0;
  z-index: 5 !important;
}

section.intro > div.intro_container{
  max-width: 1200px;
  margin: 0 auto;
  padding: calc(var(--base-padding) * 2);
}

section.intro > div.intro_container > div > h2 {
  font-size: 72px;
  font-weight: 300;
  margin: 0;
  color: black;
  /* margin: 0 calc(var(--base-margin)); */
  background-color: white;
}

div.intro_container h2 > * {
  font-size: inherit;
}

section.intro > div.intro_container h3 {
  font-weight: 300;
  font-size: 1.4em;
  line-height: 35px;
  text-align: justify;
}

section.intro > div.intro_container > ul{
  list-style: none;
  padding-left: 0;
  /* margin: calc(var(--base-margin) * 4) calc(var(--base-margin) * 4) */
}

section.intro > div.intro_container > ul > li{
  margin: var(--base-margin) 0;
}

section.intro > div.intro_container > ul > li > h3{
  font-size: 2em;
  margin: 0;
  color: black;
  font-weight: 400;
}

section.intro > div.intro_container > ul > li > p{
  font-size: 1.2em;
  font-weight: 300;
  margin: 0;
}

section.intro > div.intro_container > div.product_cards_container {
  display: flex;
  flex-direction: row;
  gap: calc(var(--base-gap) * 2);
  flex-wrap: wrap;
  margin: 0 auto;
  justify-content: center;
  padding: 20px;
}

div.product_cards_container > div > div {
  max-width: 400px;
  padding: var(--base-padding);
  display: flex;
  flex-direction: column;
}

div.product_cards_container > div > div > div.img_container {
  height: 250px;
}

div.product_cards_container > div > div > div.img_container > img {
  height: 100%;
  width: 100%;
}

section.intro > video{
  width: 100%;
}





/*==================*/
/* Services Section */
/*==================*/
section.services{
  text-align: left;
  top: 100vh;
  background-color: white;
  padding: 3em 0;
  z-index: 5 !important;
  background-color: #3615BD;
  color: #FFF2EE;
}

section.services > div.services_container{
  max-width: 1200px;
  margin: 0 auto;
  padding: calc(var(--base-padding) * 2);
}

section.services > div.services_container h3 {
  font-weight: 300;
  font-size: 1.4em;
  line-height: 35px;
  text-align: justify;
}

section.services > div.services_container h2 {
  font-size: 72px;
  font-weight: 400;
  margin: 0;
  color: inherit;
}

section.services > div.services_container h2 > * {
  font-size: inherit;
}




/*==============================*/
/* Beginning of Explore Section */
/*==============================*/

/* Left */
section.explore{
  background-color: #FFF2EE;
  color: rgb(13, 13, 95);
  display: flex;
  flex-direction: column;
  gap: calc(var(--base-gap) * 2);
  padding: calc(var(--base-padding) * 2) 0;
}

section.explore > div > div.entity {
  padding: 3em 2em;
  max-width: 1200px;
  margin: 0 auto;
  box-shadow: 2px 3px 4px rgba(0,0,0,0.4);
  background-color: white;
}

section.explore > div > div.entity > h3{
  font-size: 2.5em;
}

section.explore > div > div.entity > h3 > * {
  font-size: inherit;
}

section.explore > div > div.entity > div.explore_container{
  display: grid;
  grid-template-columns: max-content 1fr;
  gap: var(--base-gap);
  margin: 0 auto;
  position: unset;
  /* background-color: white; */
  direction: ltr;
}

div.entity > div.explore_container > div.explore_right{
  text-align: left;
}

section.explore > div:nth-of-type(even) > div.entity > div.explore_container{ 
  direction: rtl;
}

section.explore > div:nth-of-type(even) > div > div > div.explore_right {
  text-align: right;
}

div.explore_left > figure{
  margin: 0;
}

div.explore_left > figure img{
  border-radius: var(--base-border-radius);
  width: 100%;
}



section.explore > div > div.explore_container > div.explore_left > ul > li > h3:hover{
  color: #206ef3;
}

/* Right */
section.explore > div > div.explore_container > div.explore_right{
  background-color: rgba(0,0,0,0.9);
  box-shadow: 3px 4px 5px rgba(0,0,0,0.4);
  padding: calc(var(--base-padding) * 2) calc(var(--base-padding) / 2);
  color: white;
}

section.explore > div > div.explore_container > div.explore_right > div.animation_container{
  height: 350px;
  transition: all 400ms;
}

div.animation_container > img{
  height: 100%;
  transition: all 400ms;
  animation: fade-in 600ms ease-in-out;
  width: 100%;
  /* width: 100%; */
}

section.explore > div > div.explore_container >  div.explore_right > p{
  text-align: left;
  margin: calc(var(--base-margin) * 2) var(--base-margin);
  font-weight: 300;
  transition: all 400ms;
  animation: fade-in 600ms ease-in-out;

}

section.explore > div > div.entity > div.explore_container > div.explore_right > button{
  padding: 0.8em 2.4em;
  border: 1px solid white;
  background: linear-gradient(92.34deg, #FFDE17 -35.71%, #F26522 100.76%);
  color: white;
  font-weight: bold;
  transition: all 900ms;
  border-radius: calc(var(--base-border-radius) / 2);
}

section.explore > div > div.entity > div.explore_container > div.explore_right > button:hover{
  cursor: pointer;
  background: linear-gradient( 92.34deg, #F26522  -35.71%,  #FFDE17 100.76%);
}


/*===============*/
/* Teams Section */
/*===============*/
section.team{
  text-align: left;
  top: 100vh;
  background-color: white;
  padding: 3em 0;
  z-index: 5 !important;
  /* background-color: #3615BD; */
  /* color: #FFF2EE; */
  /* background-color: #FFF2EE; */
  color: rgb(13, 13, 95);
}

section.team > div.team_container{
  max-width: 1200px;
  margin: 0 auto;
  padding: calc(var(--base-padding) * 2);
}

section.team > div.team_container h3 {
  font-weight: 300;
  font-size: 1.4em;
  line-height: 35px;
  text-align: justify;
}

section.team > div.team_container h2 {
  font-size: 72px;
  font-weight: 400;
  margin: 0;
  color: inherit;
}

section.team > div.team_container h2 > * {
  font-size: inherit;
}




@media (max-width: 950px){
  main > div.hero-video-container{
    /* background-image: url(../../assets/home/images/Live-Wave.svg); */
    /* background-size: cover; */
  }

  /* main > div.hero-video-container > video{
    height: 100vh;
    transform: rotate(90deg) translateY(70%);
  } */

  div.hero-text {
    padding: 0 calc(var(--base-padding) * 2) !important;
  }
  
  div.hero-text > h2{
    font-size: 8vw;
    font-weight: 900;
  }

  section.intro > div.intro_container > div.product_cards_container {
    padding: 10px;
  }

  section.intro > div.intro_container{
    padding: calc(var(--base-padding) * 1.5);
  }

  section.intro > div.intro_container > div > h2{
    font-size: 9vw;
  }

  section.services > div.services_container > div > h2 {
    font-size: 2.5em;
  }

  section.explore > div > div.entity > div.explore_container {
    grid-template-columns: 1fr;
    max-width: 600px;
  }

  section.explore > div > div.entity > h3{
    font-size: 2em;
  }

  section.science > div.science_wrapper{
    grid-template-columns: 1fr 2.3fr;
  }

  section.science > div.science_wrapper > div.science_main > div.science_item{
    grid-template-columns: 1fr 2.3fr;
  }

  section.team > div.team_container h2 {
    font-size: 2.5em;
    font-weight: 400;
    margin: 0;
    color: inherit;
  }
}



/*================================*/
/* Media Query For Mobile Devices */
/*================================*/
@media (max-width: 750px){
  div.hero-text > h2{
    font-size: 3.2em;
  }

  div.hero-text > p{
    font-size: 1.5em;
  }

  section.explore > div > div.explore_container {
    grid-template-columns: 1fr;
  }

  div.explore_container > div.explore_right > div.animation_container > img{
    /* height: unset; */
    /* width: 100%; */
  }

  section.science > div.science_wrapper{
    grid-template-columns: 1fr;
  }

  section.science > div.science_wrapper > div.science_main > div.science_item{
    grid-template-columns: 1fr 2.3fr;
  }
}
