main.about_us_main {
  background: url(../../assets/products/geometry.png);
  background-size: cover;
}

main.about_us_main > div.page_wrapper {
  background-color: rgba(255, 255, 255, 0.8);
}

@keyframes height-up {
  from {
    transform: translateY(-100%);
  }
  to {
    transform: translateY(0);
  }
}

@keyframes fade-in {
  from {
    transform: translateY(100px);
    opacity: 0;
  }

  to {
    opacity: 100%;
    transform: translateY(0);
  }
}

main.about_us_main > div.page_wrapper > section.hero_image_container {
  position: relative;
  z-index: 0;
  animation: height-up 1s ease-in-out;
}

section.hero_image_container > img {
  width: 100%;
}

section.hero_image_container > div.hero_text_container {
  position: absolute;
  top: 30%;
  text-align: center;
}

section.hero_image_container > div.hero_text_container > h1.about_us_text {
  color: white;
  font-size: 43px;
  margin: 0;
  text-align: center;
}



/*===========================*/
/* About-Us text Sub-Section */
/*===========================*/
main.about_us_main > div.page_wrapper > section.about_text {
  padding: calc(var(--base-padding) * 2);
  max-width: 1200px;
  margin: 0 auto;
  background-color: rgba(255, 255, 255, 0.95);
}

main.about_us_main > div.page_wrapper > section.about_text > p {
  line-height: 27px;
  font-weight: 300;
  font-size: 1.3em;
  animation: fade-in 1.5s ease-in-out;
}


/*=========================*/
/* Our Mission Sub-Section */
/*=========================*/
main.about_us_main > div.page_wrapper > section.our_mission {
  padding: calc(var(--base-padding) * 2);
  max-width: 1200px;
  margin: 0 auto;
  margin-bottom: 5em;
  animation: fade-in 1.5s ease-in-out;
  background-color: rgba(255, 255, 255, 0.95);
}

main.about_us_main > div.page_wrapper > section.our_mission > h1 {
  font-size: 45px;
  text-align: center;
}

main.about_us_main > div.page_wrapper > section.our_mission > p {
  line-height: 27px;
  font-weight: 300;
  font-size: 1.3em;
}






/*================================*/
/* Media Query For Mobile Devices */
/*================================*/
@media (max-width: 900px){
  section.hero_image_container > img {
    height: 150px;
    width: 100%;
  }

  section.hero_image_container > h1 {
    left: 40% !important;
  }

  main.about_us_main > div.page_wrapper > section.about_text {
    background-color: rgba(255, 255, 255, 0.2);
  }

  main.about_us_main > div.page_wrapper > section.about_text p {
    background-color: rgba(255, 255, 255, 0.9);
    padding: 0.5em 0;
  }

  main.about_us_main > div.page_wrapper > section.our_mission{
    background-color: rgba(255, 255, 255, 0.7);
  }

  main.about_us_main > div.page_wrapper > section.our_mission p {
    background-color: rgba(255, 255, 255, 0.9);
    padding: 0.5em 0;
  }

}